import React from 'react'
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import * as bs from 'react-bootstrap'
import HeaderContainer from './header-container'
import LeftContainer from './left-container'
import RightContainer from './right-container'
import FooterContainer from './footer-container'
import Home from './home'
import About from './about'
import Help from './help'
import ShoppingCart from './cart'
import ProductDetail from './product-detail.js'
import Checkout from './checkout.js'
import Receipt from './receipt.js'
import './App.scss'
import './index.scss'


function App(props) {
  return (
    <Router>

      <bs.Container fluid className="p-0 min-vh-100 d-flex flex-column">

        <bs.Row noGutters className="flex-grow-0 flex-shrink-0 shadow-sm">
          <bs.Col className="px-0 py-0 bg-primary">
            <HeaderContainer />
          </bs.Col>
        </bs.Row>

        <bs.Row noGutters className="flex-grow-1">
          <bs.Col md="2" className="px-3 py-4 shadow bg-secondary">
            <LeftContainer />
          </bs.Col>

          <bs.Col md="8" className="bg-white">
            <Switch>
              <Route path="/static/build/about">
                <About />
              </Route>
              <Route path="/cart">
                <ShoppingCart />
              </Route>
              <Route path="/checkout">
                <Checkout />
              </Route>
              <Route path="/help">
                <Help />
              </Route>
              <Route path="/receipt">
                <Receipt />
              </Route>
              <Route path="/product/:id">
                <ProductDetail />
              </Route>
              <Route path="/category/:name">
                <Home />
              </Route>
              <Route path="/">
                <Home />
              </Route>
            </Switch>
          </bs.Col>

          <bs.Col md="2" className="px-3 py-4 shadow bg-secondary">
            <RightContainer />
          </bs.Col>
        </bs.Row>

        <bs.Row noGutters className="flex-grow-0 flex-shrink-0 shadow-sm">
          <bs.Col className="px-0 py-0 bg-primary">
            <FooterContainer />
          </bs.Col>
        </bs.Row>

      </bs.Container>
    </Router>
  )
}

export default App