import React from 'react'
import * as bs from 'react-bootstrap'
import { Link } from "react-router-dom"
import AppContext from './context.js'


function LeftContainer(props) {

    const state = React.useContext(AppContext)

    const categories = {}
    let total = 0

    for (const p of state.products) {
        if (p.category.title in categories) {
            categories[p.category.title] = categories[p.category.title] + 1
            total += 1
        }
        else {
            categories[p.category.title] = 1
            total += 1
        }
    }

    return (
        <bs.Nav className="flex-column">
            <Link to="/" className="nav-link">
                All Products ({total})
            </Link>
            {Object.keys(categories).map((name, idx) => {
                return <Link
                    key={idx}
                    to={"/category/" + name}
                    className="nav-link"
                >
                    <i>{name} ({categories[name]})</i>
                </Link>
            })}
        </bs.Nav>
    )
}


export default LeftContainer